import React, {useEffect, useState} from 'react';
import {Button, Form, Input, Select, message} from 'antd';
import type {FormInstance} from 'antd/es/form';
import axios, {AxiosResponse, AxiosError} from 'axios';
import NodeTable from './NodeTable';
import {Switch} from 'antd';
import G6 from '@antv/g6';
import {Tabs} from 'antd';
import  TreeSelectList from './TreeSelectList';
import {AndroidOutlined, AppleOutlined} from '@ant-design/icons';

const {Option} = Select;
const layout = {
    labelCol: {span: 8},
    wrapperCol: {span: 16},
};

const tailLayout = {
    wrapperCol: {offset: 8, span: 16},
};
//
const dataSources = [{"name": "dupdb", "value": "local"}, {"name": "xepdb1", "value": "remote"}];
const baseUrl=process.env.REACT_APP_BASE_URL;
function uppercaseObjectValues(obj: Record<string, any>): Record<string, any> {
    const newObj: Record<string, any> = {};

    Object.keys(obj).forEach(key => {
        const value = obj[key];

        newObj[key] = typeof value === 'string' ? value.toUpperCase() : value;
    });

    return newObj;
}

//key
function addKeyToObjects(arr: any): any {
    return arr.map((obj: any, index: any) => ({
        ...obj,
        key: index
    }));
}

const AllDependency: React.FC = () => {
        const formRef = React.useRef<FormInstance>(null);
        const [NodesData, setNodesData] = useState<any[]>([]);
        const [loading, setLoading] = useState(false);
        const [queryData, setQueryData] = useState<any>();
        const [messageApi, contextHolder] = message.useMessage();
        const [treeView, setTreeView] = useState(false);
       const [treeList, setTreeList] = useState<any[]>([]);

       // const [currentTab, setCurrentTab] = useState("1");
        // const onGenderChange = (value: string) => {
        //     switch (value) {
        //         case 'male':
        //             formRef.current?.setFieldsValue({note: 'Hi, man!'});
        //             break;
        //         case 'female':
        //             formRef.current?.setFieldsValue({note: 'Hi, lady!'});
        //             break;
        //         case 'other':
        //             formRef.current?.setFieldsValue({note: 'Hi there!'});
        //             break;
        //         default:
        //             break;
        //     }
        // };


        const onReset = () => {
            formRef.current?.resetFields();
        };

        const onFill = () => {
            formRef.current?.setFieldsValue({
                dataSource: 'local',
                owner: 'SYS',
                objectType: 'TABLE',
                objectName: 'DUAL'

            });
        };
        //   const axios = require('axios');
        const onFinish = (values: any) => {
            setLoading(true);

            let data = uppercaseObjectValues(values);//value
            setQueryData(data);
            axios({
                url: `${baseUrl}getAllDependencies`,
                method: 'POST',
                headers: {'content-type': 'application/json'},
                data: data
            })
                .then(function (response: AxiosResponse) {

                    let nodeData = addKeyToObjects(response.data);
                    setNodesData(nodeData);
                     setLoading(false);

                })
                .catch(function (error: AxiosError) {


                    setNodesData([]);
                    setLoading(false);
                    message.info("");

                });

         //
            axios({
                url: `${baseUrl}queryTreeList`,
                method: 'POST',
                headers: {'content-type': 'application/json'},
                data: data
            })
                .then(function (response: AxiosResponse) {

                    setTreeList(response.data);


                })
                .catch(function (error: AxiosError) {


                    setTreeList([]);

                    message.info(",");

                });






            if (treeView) {


            }
        };
        const switchOnChange = () => {

            if (treeView) {
                setTreeView(false);
            } else {
                setTreeView(true);
            }
        }



        // @ts-ignore
        // @ts-ignore
        return (
            <div><Form
                {...layout}
                ref={formRef}
                name="control-ref"
                onFinish={onFinish}
                style={{maxWidth: 600}}>

                {/*<Switch checkedChildren="" unCheckedChildren="" defaultChecked={false}*/}
                {/*      onChange={switchOnChange}/>*/}

                <Form.Item name="dataSource" label="" rules={[{required: true}]}>

                    <Select
                        placeholder="application.yum"

                        allowClear>
                        <Option value="local">local</Option>
                        <Option value="remote">remote</Option>

                    </Select>

                </Form.Item>


                <Form.Item name="owner" label="" rules={[{required: true}]}>

                    <Input placeholder=":SYS" />
                </Form.Item>

                <Form.Item name="objectType" label="" rules={[{required: true}]}>

                    <Input placeholder=":TABLE"/>
                </Form.Item>


                <Form.Item name="objectName" label="" rules={[{required: true}]}>

                    <Input placeholder=":DUAL"/>
                </Form.Item>


                <Form.Item {...tailLayout}>
                    <Button type="primary" htmlType="submit" loading={loading}>
                        
                    </Button>

                    <Button htmlType="button" onClick={onReset}>
                        
                    </Button>
                    <Button type="link" htmlType="button" onClick={onFill}>
                        
                    </Button>
                </Form.Item>
            </Form>



                <Tabs

                    type="card"
                    items={new Array(2).fill(null).map((_, i) => {
                        const id = String(i + 1);
                        const label = id === "1" ? "" : "";
                        return {
                            label: (
                                <span> {label}

                                 </span>
                            ),
                            key: id,
                            // children: <NodeTable NodesData={NodesData}/>
                            children:  id==="1" ? <NodeTable NodesData={NodesData}  backupFlag={false} /> :<TreeSelectList treeData={treeList}/>,
                        };
                    })}
                />


            </div>
        );
    }
;

export default AllDependency;